import { Grid } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { ListProducts } from '../../actions/productActions';
import LoadingBox from '../LoadingBox/LoadingBox';
import MessageBox from '../MessageBox/MessageBox';
import Product from '../Product/Product';
import ProductsGrid from '../ProductsGrid/ProductsGrid';
import "./HomeGrid.scss";


const HomeGrid = () => {
    const [currentTab, setCurrentTab] = useState("bestSeller");
    const dispatch = useDispatch();
    const productList = useSelector(state => state.productList)
    const { loading, error, products } = productList;

    useEffect(() => {
        dispatch(ListProducts({}))
    }, []);

    const handleClick = (e) => {
        setCurrentTab(e.target.getAttribute('name'))
    }
    let sortedProducts = [];
    if (currentTab === 'bestSeller') {
        products?.forEach(pro => sortedProducts.push(pro))
    } else {
        const dateProductVirtual = [...products];
        if (dateProductVirtual) {
            dateProductVirtual.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
            for (let i in dateProductVirtual) {
                const today = new Date();
                const productDate = new Date(dateProductVirtual[i].createdAt);
                if (((today - productDate) / (1000 * 3600 * 24)) < 25) {
                    sortedProducts.push(dateProductVirtual[i])
                }
            }
        }
    }


    console.log(products)
    return (
        <div className="HomeGrid">
            <div className="home-tab">
                <p
                    name="bestSeller"
                    onClick={handleClick}
                    className={currentTab === "bestSeller" ? "home-tab-active" : "home-tab-active"} >
                    Best Sellers
                </p>
                <p
                    name="newProducts"
                    onClick={handleClick}
                    className={currentTab === "newProducts" ? "home-tab-active" : ""}>
                    New Products
                </p>
            </div>
            <div className="home-content">
                {loading
                    ? <LoadingBox />
                    : error
                        ? <MessageBox error={error}>{error}</MessageBox>
                        : <Grid container justify="space-around">
                            {
                                sortedProducts?.map(({ _id, name, image, price, rating, numReviews }) => (
                                    <Product
                                        item
                                        key={_id}
                                        id={_id}
                                        name={name}
                                        rating={rating}
                                        price={price}
                                        image={image}
                                        numReviews={numReviews}
                                    />
                                ))
                            }
                        </Grid>
                }

            </div>

        </div>
    )
}

export default HomeGrid
